import Data from "../core/data";
import axios from "axios";
import JWT from "./jwt";

const pcors_url = "https://my.${region}.500apps.com/pcors?url=";

const server_url = "https://api.${region}.500apps.com/pbxplus/v1/node/users";

const transfer_url = "https://api.${region}.500apps.com/voxdesk/v1/transfer/${type}/outbound/${callsid}/number/${number}";

const conference_url = "https://api.${region}.500apps.com/voxdesk/v1/conference/outbound/${callsid}/number/${number}";

class Conference extends Data {
  /**
   * Method used to get list of users
   */
  async getAgents() {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = pcors_url + server_url;
    url = eval("`" + url + "`");

    let response = await axios.get(url, { headers });
    return response.data;
  }

  /**
   * Method used to transfer call
   * @param {String} callsid - CallSid
   * @param {String} number - Phone Number
   * @param {String} type - type cold or warm
   */
  // eslint-disable-next-line
  async transferCall(callsid, type, number) {
    let headers = await JWT.setHeader();
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = pcors_url + transfer_url;
    url = eval("`" + url + "`");
    let response = await axios.get(url, { headers });
    console.log(response);
  }

  debounce(callback, delay) {
    let timer = null;
    return function() {
      clearTimeout(timer);
      var args = arguments;
      let context = this;
      timer = setTimeout(function() {
        callback.apply(context, args);
      }, delay);
    };
  }

  // eslint-disable-next-line
  async conferenceCall(number, callsid, from_number, name) {
    let headers = await JWT.setHeader();
    let body = {
      from_number: from_number,
      conference_name: name,
    };
    // eslint-disable-next-line
    let region = this.getRegion();
    let url = pcors_url + conference_url;
    url = eval("`" + url + "`");
    let response = await axios.post(url, body, { headers });
    return response.data;
  }
}

export default new Conference();
