<template>
  <div v-if="connection">
    <div class="btn-group-vertical dailer-pad position-relative" role="group" aria-label="Basic example">
      <div class="input-group phone-pad-section align-items-center mb-3 mt-5 w-100">
        <input placeholder="Enter phone number" class="bg-transparent border-0 font-weight-normal form-control h2 mb-0 p-0 text-center phno-placeholder" v-model="phone_number" /><i
          class="fal fa-arrow-up pb-1"
        ></i>
      </div>
      <div class="d-flex justify-content-between w-100 mb-3 pb-1">
        <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number" @click="conferenceCall()" title="Conference">
          <div class="align-items-center d-flex flex-column justify-content-center text-center">
            <div class="mr-n1">
              <i class="fe fe-user-plus"></i>
            </div>
            <p class="mb-0 text">Add</p>
          </div>
        </b-button>
        <b-button type="button" variant="secondary" class="rounded-circle dailer-pad-number" @click="$emit('showkeypad')" title="Dial Pad" v-b-tooltip:hover>
          <div class="align-items-center d-flex flex-column justify-content-center text-center">
            <div>
              <i class="fe fe-grid"></i>
            </div>
            <p class="mb-0 text">keypad</p>
          </div>
        </b-button>

        <b-button
          type="button"
          variant="secondary"
          class="rounded-circle dailer-pad-number"
          @click="
            $emit('mutecall');
            is_mute = !is_mute;
          "
          title="Mute"
          v-b-tooltip:hover
        >
          <div class="align-items-center d-flex flex-column justify-content-center text-center">
            <div>
              <i :class="`fe ${is_mute ? 'fe-mic' : 'fe-mic-off text-danger'}`"></i>
            </div>
            <p class="mb-0 text " v-if="is_mute">Mute</p>
            <p class="mb-0 text text-danger" v-else>Unmute</p>
          </div>
        </b-button>
      </div>
    </div>
  </div>
</template>

<script>
  import conference from "../../../js/dialer/conference";
  export default {
    props: {
      connection: Object,
      twilioMute: Boolean,
    },
    data() {
      return {
        phone_number: "",
        conference_name: "",
        from_number: "",
        is_mute: true,
      };
    },
    mounted: function() {
      this.is_mute = !this.twilioMute;
    },
    methods: {
      async conferenceCall() {
        this.$emit("conferencecall", this.phone_number);
        let response = await conference.conferenceCall(this.phone_number, this.connection.parameters.CallSid, this.from_number, this.conference_name);
        this.conference_name = response.conference_name;
        this.from_number = response.from_number;
      },
    },
  };
</script>
<style>
  .text-danger {
    color: #ef4b69 !important;
  }
</style>
